import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import Workout from '../../components/workout';
import WorkoutWeek from '../../components/workout/week';
import Teams from '../../components/team/teams';

const initialState = {
	isLoading: false,
};

@observer
class Workouts extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    startTracking = () => {
		const { workoutStore } = this.props.stores;
        workoutStore.cordovaStartTracking();
    }

    resumeTracking = () => {
		const { workoutStore } = this.props.stores;
        workoutStore.cordovaResumeTracking();
    }

    pauseTracking = () => {
		const { workoutStore } = this.props.stores;
        workoutStore.cordovaPauseTracking();
    }

    finishTracking = () => {
		const { workoutStore } = this.props.stores;
        workoutStore.cordovaSaveTracking();
    }

    componentDidMount() {
		const { workoutStore, appState } = this.props.stores;
        appState.checkCordova();
        workoutStore.cordovaInitTracking();   
    }

	render() {
		const { isLoading } = this.state;
		const { userStore, appState, workoutStore } = this.props.stores;
        const { currentTeam, totalTime = 0, currentSpeed = 0, totalDistance = 0, isRunning } = workoutStore;
		const { darkmode } = appState;
		const { week, year = util.getYear() } = this.props;
		const {
            workoutsDay, workoutSummaryWeekDay,
		} = userStore;

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='navbar-themed fixed-top text-center' style='z-index: 10000;'>
                    {isRunning ? <>
                        <h1 class='text-success'>
                            Tracking
                        </h1>
                    </> : <>
                        {totalTime > 0 ? <>
                            <h1 class='text-danger'>
                                Stopped
                            </h1>
                        </> : <>
                            <h1 class='text-warning'>
                                Ready to start
                            </h1>
                        </>}
                    </>}
                </div>
				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom'>
						<Teams stores={this.props.stores} callback={this.loadWorkouts} key={`teams-${currentTeam}`} />
					</div>

                    {/* <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<WorkoutGraph stores={this.props.stores} isLoading={isLoading} />
					</div> */}

                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2 text-center'>
                        Time
                        <div class='display-3'>
                            {util.secToHms(totalTime)}
                        </div>
                    </div>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2 text-center'>
                        Speed
                        <div class='display-3'>
                            {util.format(currentSpeed * 3.6, 2)} km/t
                        </div>
                    </div>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2 text-center'>
                        Distance
                        <div class='display-3'>
                            {util.format(totalDistance / 1000, 2)} km
                        </div>
                    </div>
                    <div class='navbar-themed fixed-bottom text-center' style='z-index: 10000;'>
                        {isRunning ? <>
                            <button class='btn btn-link' onClick={this.pauseTracking}>
                                <i class='fas fa-pause-circle text-danger' style='font-size: 60px;' /><br />
                                <small>Pause</small>
                            </button>
                        </> : <>
                            {totalTime > 0 ? <>
                                <button class='btn btn-link' onClick={this.resumeTracking}>
                                    <i class='fas fa-play-circle text-danger' style='font-size: 60px;' /><br />
                                    <small>Resume</small>
                                </button>                            
                                <button class='btn btn-link'  onClick={this.finishTracking}>
                                    <i class='fas fa-check-circle text-success' style='font-size: 60px;' /><br />
                                    <small>Finish</small>
                                </button>                            
                            </> : <>
                                <button class='btn btn-link' onClick={this.startTracking}>
                                    <i class='fas fa-play-circle text-danger' style='font-size: 60px;' /><br />
                                    <small>Start</small>
                                </button>                            
                            </>}
                        </>}
                    </div>
				</div>
			</div>
		);
	}
}

export default Workouts;
